﻿@import "../mixins";

.reviews {
	width: 100%;
	height: 753px;
	margin-top: 0px;
	background: inherit;
	position: relative;

	.reviews_title {
		@include blockTitle(120px, 115px, 60px, 209px, nowrap);
	}

	.reviews_title::before {
		content: '';
		position: absolute;
		left: -113px;
		top: 27px;
		height: 3px;
		width: 58px;
		background-color: $arrowcolor;
	}
}

.reviews_group {
	height: 517px;
	width: 1180px !important;
	margin-left: 113px;
	display: flex;
	position: relative;
	background: inherit;
	justify-content: space-between;
	cursor: pointer;
}

.reviews_group_item1,
.reviews_group_item2,
.reviews_group_item3,
.reviews_group_item4,
.reviews_group_item5,
.reviews_group_item6,
.reviews_group_item7,
.reviews_group_item8,
.reviews_group_item9,
.reviews_group_item10,
.reviews_group_item11,
.reviews_group_item12 {
	display: inline-block;
	width: 550px !important;
	height: 440px;
	position: relative;
	background: #fff;
}

.reviews_group_item_title {
	height: 40px;
	width: 129px;
	min-width: 129px;
	white-space: nowrap;
	color: #333333;
	font-family: $desk-bold-raleway;
	font-size: 20px;
	line-height: 30px;
	margin-top: 40px;
	margin-left: 40px;
	margin-bottom: 0px;
}

.reviews_group_item_location {
	display: inline-block;
	height: 40px;
	width: 311px;
	min-width: 311px;
	color: #333333;
	font-family: $desk-bold-raleway;
	font-size: 20px;
	line-height: 30px;
	margin-top: -30px;
	margin-left: 40px;
	margin-bottom: 0px;
	white-space: nowrap;
}

.reviews_group_item_review {
	display: inline-block;
	height: 280px;
	max-height: 470px;
	width: 470px;
	color: #333333;
	font-family: $desk-regular-raleway;
	font-size: 16px;
	line-height: 28px;
	white-space: pre-line;
	margin-top: -20px;
	margin-left: 40px;
}

.reviews_group .slick-dots {
	padding-bottom: 10px;
  bottom: -35px;
  left: 0%;
	font-family: $desk-regular-proxima;
	font-size: 14px;
	line-height: 17px;
}

.reviews_group .slick-dots li {
	opacity: 0.5;
	color: #A5A5A5;
	height: 17px;
	width: 6px;
	font-family: $desk-regular-proxima;
	font-size: 14px;
	line-height: 17px;
	left: -1.5% !important;
	margin-right: 10px;
	margin-bottom: 24px;  
}

.reviews_group .slick-dots li.slick-active {
	color: #E78B48;
	opacity: 1;
}

.reviews_group .slick-dots li {
	display: none;
}

.reviews_group .slick-dots li.slick-active,
.reviews_group .slick-dots li.slick-active + li,
.reviews_group .slick-dots li.slick-active + li + li,
.reviews_group .slick-dots li.slick-active + li + li + li,
.reviews_group .slick-dots li.slick-active + li + li + li + li,
.reviews_group .slick-dots li.slick-active + li + li + li + li + li {
	display: inline-block !important;
}

.reviews_group .slick-dots li:nth-last-child(1),
.reviews_group .slick-dots li:nth-last-child(2),
.reviews_group .slick-dots li:nth-last-child(3),
.reviews_group .slick-dots li:nth-last-child(4),
.reviews_group .slick-dots li:nth-last-child(5),
.reviews_group .slick-dots li:nth-last-child(6) {
	display: inline-block;
}

.reviews_group .slick-dots li.slick-active ~ li:nth-last-child(1),
.reviews_group .slick-dots li.slick-active ~ li:nth-last-child(2),
.reviews_group .slick-dots li.slick-active ~ li:nth-last-child(3),
.reviews_group .slick-dots li.slick-active ~ li:nth-last-child(4),
.reviews_group .slick-dots li.slick-active ~ li:nth-last-child(5),
.reviews_group .slick-dots li.slick-active ~ li:nth-last-child(6) {
	display: none;
}

.reviews_group .slick-arrow {
	z-index: 9;
	padding: 20px;
}

.reviews_group .slick-prev {
	top: 97.5%;
	left: 38.3%;
}

.reviews_group .slick-next {
	top: 97.4%;
	right: 41.7%;
}

.reviews_group .slick-prev:before {
	content: "❮";
	font-size: 10px;
	color: #A5A5A5;
}

.reviews_group .slick-next:before {
	content: "❯";
	color: #A5A5A5;
	font-size: 10px;
}

.reviews_group_arrows_far_next {
	position: absolute;
	color: #A5A5A5;
	font-size: 10px;
	padding: 10px;
	top: 729.5px;
	right: 548px;
	z-index: 10;
	cursor: pointer;
}

.reviews_group_arrows_far_prev {
	position: absolute;
	color: #A5A5A5;
	font-size: 10px;
	padding: 10px;
	top: 729.5px;
	left: 550px;
	z-index: 10;
	cursor: pointer;
}

.reviews_group_arrows_far_prev::before {
	content: '|';
	position: absolute;
	top: 12px;
	right: 15px;
}

.reviews_group_arrows_far_next::after {
	content: '|';
	position: absolute;
	top: 12px;
	left: 15px;
}

.review_group_pages,
.review_group_pages_mobile {
	display: inline-block;
	position: absolute;
	bottom: 0;
	right: 113px;
	font-style: normal;
	height: 17px;
	width: 108px;
	color: $arrowcolor;
	font-family: $desk-bold-proxima;
	font-size: 14px;
	font-weight: bold;
	line-height: 17px;
	white-space: nowrap;
}

.review_group_pages_mobile {
	display: none;
}

@-moz-document url-prefix() {
	.review_group_pages {
		right: 120px;
	}
}

