﻿@import "../mixins";

// MEDIA QUERY 320

@media screen and (max-width: 320px) {
	.our_mission {
		width: 100%;
		height: 1380px;
		background: inherit;
		position: relative;

		.our_mission_title {
			@include blockTitleMob2(82px, 35px, 0px, 273px, nowrap);

			position: unset;
		}

		.our_mission_title::before {
			content: '';
			position: absolute;
			left: 0px;
			top: 95px;
			height: 3px;
			width: 20px;
			background-color: $arrowcolor;
		}
	}

	.our_mission_slogan {
		height: 168px;
		width: 250px;
		color: #333333;
		font-family: $desk-regular-proxima;
		font-size: 14px;
		line-height: 24px;
		margin-left: 35px;
		margin-top: 20px;
		white-space: unset;
	}

	.our_mission_title_second {
		display: inline-block;
		margin-left: 35px;
		margin-top: 40px;
		height: 136px;
		width: 255px;
		color: #333;
		font-family: $desk-bold-raleway;
		font-size: 24px;
		font-weight: unset;
		line-height: 34px;
		margin-bottom: 0px;

		br {
			display: none;
		}
	}

	.our_mission_title_second::before {
		content: '';
		position: absolute;
		left: -35px;
		top: 13px;
		height: 3px;
		width: 20px;
		background-color: $arrowcolor;
	}

	.our_mission_programs {
		list-style-type: none;
		display: inline-block;
		margin-top: 39px;
		margin-left: -17px;
		width: 270px;
		height: 120px;
		color: #333333;
		font-family: $desk-regular-proxima;
		font-size: 14px;
		line-height: 24px;

		li {
			position: relative;
		}

		.program_first {
			margin-bottom: 40px;
			position: relative;
			height: 192px;

			p {
				margin-top: 0px;
				margin-left: 30px;
				width: 270px;

				br {
					display: block;
				}
			}
		}

		.program_second p {
			display: inline-block;
			margin-top: -23px;
			width: 240px;
			margin-left: 31px;
			white-space: pre-line;
		}
	}

	.our_mission_programs .program_first span {
		margin-left: 30px;
		display: inline-block;
		margin-bottom: 0px;
		width: 219px;
		white-space: pre-line;
	}

	.our_mission_programs .program_second span {
		margin-left: 30px;
		display: inline-block;
		margin-bottom: 0px;
	}

	.our_mission_programs li::before {
		content: '';
		position: absolute;
		left: 12px;
		top: 8px;
		width: 7px;
		height: 7px;
		background: $arrowcolor;
	}

	.our_mission cite {
		position: absolute;
		top: 930px;
		right: 0px;
		left: 35px;
		height: 66px;
		width: 250px;
		padding-top: 23px;
		border-top: 3px solid $arrowcolor;
		color: #333;
		font-family: $desk-bold-proxima;
		font-size: 14px;
		line-height: 22px;
	}

	.our_mission cite:nth-of-type(2) {
		top: 1080px;
	}

	.our_mission cite:nth-of-type(3) {
		padding-top: 0px;
		top: 1275px;
		width: 250px;
		white-space: pre-line;
	}
}

