@import "../mixins";

.our_team_mobile {
	width: 100%;
	height: 1036px;
	margin-top: 0;
	margin-bottom: 20px;
	background: inherit;
	position: relative;
	display: none;

	.our_team_title_mobile {
		@include blockTitle(0px, 115px, 40px, 209px, nowrap);
	}

	.our_team_title_mobile::before {
		content: '';
		position: absolute;
		left: -113px;
		top: 27px;
		height: 3px;
		width: 58px;
		background-color: $arrowcolor;
	}
}

.our_team_tip_mobile {
	display: block;
	height: 56px;
	width: 636px;
	color: #333333;
	font-family: $desk-regular-raleway;
	font-size: 16px;
	line-height: 28px;
	margin-left: 114px;
	margin-bottom: 24px;
}

.our_team_group_mobile {
	display: flex;
	width: 1140px;
	height: 752px;
	margin-left: 114px;
	min-height: 752px;
	flex-wrap: wrap;
	justify-content: space-between;

	.our_team_group_item_mobile {
		width: 353px;
		height: 353px;
		margin-top: 38px;
		position: relative;
		cursor: pointer;
	}
}

.our_team_group_item_mobile:hover .member_card_mobile,
.our_team_group_item_mobile:focus .member_card_mobile {
	display: block;
}

.member_intro_mobile {
	width: 100%;
	height: 100%;
}

.member_intro_mobile h3 {
	height: 21px;
	width: 119px;
	color: #FFF;
	font-family: $desk-bold-raleway;
	font-size: 18px;
	line-height: 21px;
	margin-top: 293px;
	margin-left: 40px;
	white-space: nowrap;
}

.member_mobile1,
.member_mobile4,
.member_mobile7,
.member_mobile10 {
	background: rgba(190, 190, 190, 0.5);
}

.member_mobile2,
.member_mobile5,
.member_mobile8,
.member_mobile11 {
	background: rgba(190, 190, 190, 0.5);
}

.member_mobile3,
.member_mobile6,
.member_mobile9,
.member_mobile12 {
	background: rgba(190, 190, 190, 0.5);
}

.member_mobile7,
.member_mobile8,
.member_mobile9,
.member_mobile10,
.member_mobile11,
.member_mobile12 {
	display: none;
}

.member_card_mobile {
	display: inline-block;
	position: absolute;
	top: 0;
	width: 100%;
	height: 100%;
	background: $Bghover-color;
	display: none;
}

.member_card_mobile .member_card_title_mobile {
	margin-top: 63px;
	margin-left: 40px;
	display: inline-block;
	margin-bottom: 0;
	height: 24px;
	width: 158px;
	color: #FFFFFF;
	font-family: $desk-bold-raleway;
	font-size: 20px;
	line-height: 24px;
	white-space: nowrap;
}

.member_card_tip_mobile {
	display: inline-block;
	height: 69px;
	width: 258px;
	margin-top: -5px;
	margin-left: 41px;
	color: #FFFFFF;
	font-family: $desk-regular-raleway;
	font-size: 16px;
	line-height: 23px;
	white-space: pre-line;
	position: relative;
}

.member_card_tip_mobile::after {
	content: '';
	position: absolute;
	top: 131px;
	height: 1px;
	width: 273px;
	opacity: 0.4;
	background-color: #FFFFFF;
}

.member_card_mobile .phone_contacts_mobile {
	display: inline-block;
	margin-top: 81px;
	margin-left: 41px;
	height: 28px;
	width: 258px;
	color: #FFFFFF;
	font-family: $desk-regular-proxima;
	font-size: 16px;
	line-height: 28px;
}

.member_card_mobile .email_contacts_mobile {
	display: inline-block;
	margin-top: 0px;
	margin-left: 41px;
	height: 28px;
	width: 258px;
	color: #FFFFFF;
	font-family: $desk-regular-proxima;
	font-size: 16px;
	line-height: 28px;
}

.show_more_members_section_mobile {
	display: inline-block;
	margin-top: 76px;
	margin-left: 41.9%;
	width: 500px;
	position: relative;
	cursor: pointer;
	z-index: 50;

	&:hover,
	&:focus {
		margin-left: 42.9%;
		transition: .4s;
		opacity: 0.7;
	}
}

.show_more_members_section_mobile ul {
	list-style-type: none;
	display: inline-block;
	width: 200px;
	position: absolute;
	cursor: pointer;

	li {
		width: 3px;
		height: 3px;
		background: $arrowcolor;
		margin-right: 2px;
		display: inline-block;
	}
}

.show_more_members_section_mobile .show_more_members_mobile {
	width: 200px;
	color: $arrowcolor;
	font-family: $desk-bold-raleway;
	font-size: 14px;
	line-height: 16px;
	background: none !important;
	border: none !important;
	outline: none !important;
	position: absolute;
	top: 13px;
	left: 27px;
	cursor: pointer;
}

