﻿@import "../mixins";

.about_us_news {
	width: 100%;
	height: 271px;
	margin-top: 65px;
	background: inherit;
	position: relative;

	.about_us_news_title {
		@include blockTitle(0px, 115px, 80px, 209px, nowrap);
	}

	.about_us_news_title::before {
		content: '';
		position: absolute;
		left: -113px;
		top: 27px;
		height: 3px;
		width: 58px;
		background-color: $arrowcolor;
	}
}

.about_us_news_carousel {
	width: 1140px;
	height: 153px;
	margin-left: 113px;

	div {
		position: relative;
		width: 100%;
		height: 100%;

		a {
			position: absolute;
			top: 0px;

			img {
				max-width: 100%;
				filter: grayscale(100%);
				opacity: 0.5;
				cursor: pointer;

				&:hover,
				&:focus {
					filter: none;
					opacity: 1;
				}
			}
		}
	}
}

.about_us_news_carousel .to_other_news {
	float: right;
	display: block;
	margin-right: 20px;
	margin-top: 25px;
	color: $arrowcolor;
	position: relative;
	font-family: $desk-bold-raleway;
	font-size: 14px;
	line-height: 16px;
	text-decoration: none;
	white-space: nowrap;
}

.about_us_news_carousel .to_other_news::after {
	content: '';
	position: absolute;
	top: 1px;
	left: 113.5px;
	width: 12px;
	height: 13px;
	background: url("../img/svg/arrow_right.svg") no-repeat center;
}

.about_us_news_carousel div a:nth-of-type(1) {
	top: 20px;
	left: 0px;
}

.about_us_news_carousel div a:nth-of-type(2) {
	right: 608px;
}

.about_us_news_carousel div a:nth-of-type(3) {
	top: 0px;
	right: 278px;
}

.about_us_news_carousel .slick-dots {
	bottom: 0px;
	left: 0.1%;
	padding-bottom: 10px;
	cursor: pointer;
	z-index: 20;
}

.about_us_news_carousel .slick-dots li {
	height: 3px;
	width: 25px;
	opacity: 0.5;
	margin-right: 1px;
	background-color: #A5A5A5;
}

.about_us_news_carousel .slick-dots li.slick-active {
	background-color: #E78B48;
	opacity: 1;
}

#slick-slide-control00::before,
#slick-slide-control01::before,
#slick-slide-control02::before {
	content: '';
	display: none;
}

