@media screen and (min-width: 1101px) and (max-width: 1279px) {
	.about_us_news {
		width: 100%;
		height: 271px;
		margin-top: 70px;
		background: inherit;
		position: relative;

		.about_us_news_title {
			@include blockTitle(0px, 115px, 80px, 209px, nowrap);
		}

		.about_us_news_title::before {
			content: '';
			position: absolute;
			left: -113px;
			top: 27px;
			height: 3px;
			width: 58px;
			background-color: $arrowcolor;
		}
	}

	.about_us_news_carousel {
		width: 1050px;
		height: 153px;
		margin-left: 10%;

		div {
			position: relative;
			width: 100%;
			height: 100%;

			a {
				position: absolute;
				top: 0px;

				img {
					max-width: 100%;
					filter: grayscale(100%);
					opacity: 0.5;
					cursor: pointer;
				}
			}
		}
	}

	.about_us_news_carousel .to_other_news {
		float: right;
		margin-right: 110px;
		margin-top: 25px;
		color: $arrowcolor;
		position: relative;
		font-family: $desk-bold-raleway;
		font-size: 14px;
		line-height: 16px;
		text-decoration: none;
	}

	.about_us_news_carousel .to_other_news::after {
		content: '';
		position: absolute;
		top: 1px;
		left: 113.5px;
		width: 12px;
		height: 13px;
		background: url("../img/svg/arrow_right.svg") no-repeat center;
	}

	.about_us_news_carousel div a:nth-of-type(1) {
		top: 20px;
		left: 0px;
	}

	.about_us_news_carousel div a:nth-of-type(2) {
		right: 530px;
	}

	.about_us_news_carousel div a:nth-of-type(3) {
		top: 0px;
		right: 248px;
	}

	.about_us_news_carousel .slick-dots {
		bottom: 0px;
		left: 0.1%;
		padding-bottom: 10px;
		cursor: pointer;
	}

	.about_us_news_carousel .slick-dots li {
		height: 3px;
		width: 25px;
		opacity: 0.5;
		margin-right: 1px;
		background-color: #A5A5A5;
	}
}
