﻿@import "../mixins";

// DESKTOP TABS STYLE

.our_activity {
	position: relative;
	display: inline-block;
	height: 574px;
	width: 100%;
	margin-bottom: 100px;
	margin-top: 50px;

	.our_activity_title {
		display: inline-block;
		margin-left: 113px;
		margin-top: 55px;
		height: 58px;
		width: 500px;
		color: #333;
		font-family: "Raleway-Bold", sans-serif;
		font-size: 48px;
		font-weight: bold;
		white-space: nowrap;
		line-height: 58px;
		margin-bottom: 12px;
	}

	.our_activity_title::before {
		content: '';
		position: absolute;
		left: 0;
		top: 82px;
		height: 3px;
		width: 58px;
		background-color: #E78B48;
	}
}

.our_activity_tip {
	display: inline-block;
	color: #333333;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 16px;
	font-weight: 400;
	line-height: 26px;
	word-spacing: 1px;
	margin-left: 116px;
  white-space:pre-line;
}

.our_activity_item_group {
	display: flex;
	justify-content: space-between;
	margin-top: 59px;
	margin-left: 114px;
	width: 1140px;
	height: 256px;
	position: relative;

	div {
		position: relative;
		font-family: 'Raleway-Bold', sans-serif;
		height: 276px;
		width: 550px;
		background-color: #FFF;
		box-shadow: 4px 4px 30px 0 rgba(209, 209, 209, 0.2);
	}
}

.our_activity_item_group_item1 h2,
.our_activity_item_group_item2 h2 {
	margin-left: 40px;
	margin-top: 40px;
	height: 24px;
	width: 99px;
	color: #333;
	font-size: 20px;
	font-weight: bold;
	line-height: 24px;
}

.our_activity_item_group_item1 ul,
.our_activity_item_group_item2 ul {
	list-style-type: none;
	width: 130px;
	margin-top: 40px;
	margin-left: 20px;
	font-family: 'Raleway-Regular', sans-serif;

	li {
		margin-top: 20px;
		height: 22px;
		width: 130px;
		color: #333;
		font-size: 16px;
		line-height: 22px;
		position: relative;
		cursor: pointer;
	}
}

.our_activity_item_group_item2 ul {
	width: 149px;
	margin-top: 65px;

	li {
		color: #333;
		width: 149px;
		font-weight: normal;
		margin-bottom: 20px;
	}
}

.active_tab {
	color: #E78B48 !important;
	font-weight: bold !important;
	white-space: nowrap;
}

.active_tab::before {
	content: '';
	position: absolute;
	top: 8px;
	left: -58px;
	height: 3px;
	width: 40px;
	background-color: #E78B48;
}

.active_tab_second {
	color: #E78B48 !important;
	font-weight: bold !important;
	white-space: nowrap;
}

.active_tab_second::before {
	content: '';
	position: absolute;
	top: 8px;
	left: -58px;
	height: 3px;
	width: 40px;
	background-color: #E78B48;
}

.our_activity_item_group_item1 ul::after,
.our_activity_item_group_item2 ul::after {
	content: '';
	position: absolute;
	top: 0;
	left: 255px;
	height: 276px;
	width: 1px;
	opacity: 0.25;
	background-color: #A5A5A5;
}

.our_activity_item_group_item1 span,
.our_activity_item_group_item2 span {
	display: inline-block;
	position: absolute;
	top: 40px;
	right: 40px;
	height: 140px;
	width: 214px;
	color: #333;
	font-style: normal;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 16px;
	line-height: 28px;
}

.our_activity_item_group_item1 .during_treatment_tab_info,
.our_activity_item_group_item1 .after_treatment_tab_info,
.our_activity_item_group_item2 .migrants_pension_tab_info {
	display: none;
}

.our_activity_item_group_item1 i,
.our_activity_item_group_item2 i {
	font-style: normal;
	height: 16px;
	min-width: 118px;
	color: #E78B48;
	font-family: inherit;
	font-size: 14px;
	font-weight: 700;
	line-height: 16px;
	position: absolute;
	bottom: 40px;
	right: 136px;

	a {
		text-decoration: none;
		color: inherit;
	}
}

.our_activity_item_group_item1 i::after,
.our_activity_item_group_item2 i::after {
	content: '';
	position: absolute;
	top: 2px;
	left: 130px;
	width: 12px;
	height: 13px;
	background: url("../img/svg/arrow_right.svg") no-repeat center;
}

.our_activity_item_group_mobile {
	display: none;
}

