﻿@import "../mixins";

.about_us_section {
	display: inline-block;
	margin-top: 30px;
	margin-bottom: 0;
	border-top: 2px solid $arrowcolor;
	position: relative;
	width: 100%;
	height: 400px;
	background: url("../img/content/About_us_bg.jpg") no-repeat center;

	.about_us_section_title {
		display: inline-block;
		margin-top: 171px;
		margin-left: 112px;
		height: 58px;
		width: 209px;
		color: #FFF;
		font-family: $desk-bold-raleway;
		font-size: 48px;
		line-height: 58px;
	}
}

.about_us_video_play {
	background: #fff;
	border-radius: 50%;
	width: 60px;
	height: 60px;
	float: right;
	margin-top: 171px;
	margin-right: 230px;
	position: relative;
	cursor: pointer;

	a.popup {
		position: absolute;
		top: 30%;
		left: 0;
		right: 0;
		bottom: 0;
		margin-left: auto;
		margin-right: auto;
		width: 20px;
		height: 25px;
		display: inline-block;
		text-decoration: none;
		cursor: pointer;

		img {
			width: 100%;
			height: 100%;
		}
	}
}

.about_us_video_play::after {
	content: 'Дивитись вiдео';
	position: absolute;
	top: 22px;
	left: 70px;
	height: 16px;
	width: 107px;
	color: #FFF;
	font-family: $desk-bold-raleway;
	font-size: 14px;
	line-height: 16px;
}

#about_us_video_view {
	display: inline-block;
	width: 100%;
	height: 100%;
	position: fixed;
	left: 0%;
	top: 0%;
	background: rgba(190, 190, 190, 0.5);
	display: none;
	z-index: 5001;

	iframe {
		display: inline-block;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		width: 800px;
		height: 400px;
		margin: auto;
		border: unset;
		cursor: pointer;
	}
}

#about_us_video_view .about_us_video_hide {
	float: right;
	margin-right: 15px;
	margin-top: 25px;
	position: relative;
	width: 30px;
	height: 30px;
	background: transparent;
	cursor: pointer;
}

.about_us_video_hide::before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	background: #fff;
	width: 30px;
	height: 3px;
	transform: rotate(45deg);
}

.about_us_video_hide::after {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	background: #fff;
	width: 30px;
	height: 3px;
	transform: rotate(135deg);
}

