@import "partials/page_header";
@import "partials/page_footer";
@import "partials/header_top_sms_pop_up";
@import "partials/mixins";
@import "partials/about_us_partials/zaporuka_projects_help_form";
@import "partials/about_us_partials/about_us_section";
@import "partials/about_us_partials/our_mission";
@import "partials/about_us_partials/our_team_mobile";
@import "partials/about_us_partials/about_our_team";
@import "partials/about_us_partials/about_our_activity";
@import "partials/about_us_partials/about_us_news";
@import "partials/about_us_partials/about_us_reviews";
@import "partials/about_us_partials/about_us_help_section";

.home_header_bottom_nav li a sup {
	top: -5.5px !important;
}

.our_mission {
	width: 100%;
	height: 838px;
	background: inherit;
	position: relative;

	.our_mission_title {
		@include blockTitle(120px, 115px, 0px, 273px, nowrap);
	}

	.our_mission_title::before {
		content: '';
		position: absolute;
		left: -113px;
		top: 27px;
		height: 3px;
		width: 58px;
		background-color: $arrowcolor;
	}
}

.our_mission_slogan {
	display: block;
	margin-top: 15px;
	height: 84px;
	width: 750px;
	color: #333333;
	font-family: $desk-regular-raleway;
	white-space: pre-line;
	font-size: 16px;
	line-height: 28px;
	margin-left: 115px;
	margin-bottom: 0;
}

.our_mission_title_second {
	margin-top: 82px;
	height: 116px;
	width: 800px;
	color: #333333;
	font-family: $desk-bold-raleway;
	font-size: 48px;
	line-height: 58px;
	margin-left: 114px;
	position: relative;
	margin-bottom: 0;
}

.our_mission_title_second::before {
	content: '';
	position: absolute;
	left: -113px;
	top: 30px;
	height: 3px;
	width: 58px;
	background-color: $arrowcolor;
}

.our_mission_programs {
	list-style-type: none;
	display: inline-block;
	margin-top: 43px;
	margin-left: 76px;
	width: 717px;
	color: #333333;
	font-family: $desk-regular-raleway;
	font-size: 16px;
	line-height: 28px;

	li {
		position: relative;
	}

	.program_first {
		margin-bottom: 40px;
		position: relative;

		p {
			margin-top: 10px;

			br {
				display: none;
			}
		}
	}

	.program_second p {
		margin-top: 10px;

		br {
			display: none;
		}
	}
}

.our_mission_programs .program_first span {
	margin-left: 30px;
	display: inline-block;
	margin-bottom: 0px;
}

.our_mission_programs .program_second span {
	margin-left: 30px;
	display: inline-block;
	margin-bottom: 0px;
}

.our_mission_programs li::before {
	content: '';
	position: absolute;
	left: -2px;
	top: 11px;
	width: 7px;
	height: 7px;
	background: $arrowcolor;
}

.our_mission cite {
	position: absolute;
	top: 145px;
	right: 110px;
	height: 90px;
	width: 295px;
	padding-top: 28px;
	border-top: 3px solid $arrowcolor;
	color: #333333;
	font-family: $desk-bold-raleway;
	font-style: normal;
	font-size: 20px;
	line-height: 30px;
}

.our_mission cite:nth-of-type(2) {
	top: 325px;
}

.our_mission cite:nth-of-type(3) {
	padding-top: 0px;
	top: 565px;
	width: 295px;
	white-space: pre-line;
}

.page_footer {
	margin-top: 100px;
}

@media screen and (max-width: 360px) {
	.page_footer {
		margin-top: 40px !important;
	}
}

// Footer MEDIA QUERY 320-740

@media screen and (min-width: 360px) and (max-width: 740px) {
	.page_footer {
		margin-top: 60px !important;
	}
}

// Footer MEDIA QUERY 741-1021

@media screen and (min-width: 741px) and (max-width: 1021px) {
	.page_footer {
		margin-top: -100px !important;
	}
}

// Footer MEDIA QUERY 1022-1150

@media screen and (min-width: 1022px) and (max-width: 1150px) {
	.page_footer {
		margin-top: -100px !important;
	}
}
